body {
  font-family: Roboto, sans-serif;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header-container {
  background-color: #4c73c8;
  padding: 20px 0;
}

.search-form {
  justify-content: center;
  display: flex;
}

.input-form {
  padding: 10px;
  box-shadow: 0 20px 30px -10px #26394d;
}

.input-form::-ms-input-placeholder {
  font-size: 15px;
}

.input-form::placeholder {
  font-size: 15px;
}

.input-wrapper {
  position: relative;
}

.search-btn {
  width: 37px;
  height: 36px;
  z-index: 3;
  color: #3f4656;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 50%;
  right: 1px;
  transform: translate(0, -50%);
}

.search-icon {
  fill: currentColor;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 20px;
  padding: 20px 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 7.5px);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.info {
  justify-content: space-between;
  padding: 20px 7px;
  display: flex;
}

.info-item {
  font-size: 15px;
}

